import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Row, Col, Card, Nav, Form, Button, Jumbotron, Container} from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import pokeball from "../"

// Components
import Pokemon from '../components/Pokemon';
import Loading from '../components/Loading';

const Homepage = () => {
    
    
    let history = useHistory();

    const gen1 = () => {
        history.push('/gen1')
      }
      const gen2 = () => {
        history.push('/gen2')
      }
      const gen3 = () => {
        history.push('/gen3')
      }
      
    return (
        <>
            <div style = {{paddingTop:"50px"}}>
            <Jumbotron fluid style = {{marginBottom:"50px", borderRadius: "10px", backgroundColor:"#f27c7c", color:"white" }}>
        <Container>
       
            <h1>A React Pokédex</h1>
            <p>
                A Simple Pokédex Built Using React and Bootstrap and Populated by the PokeAPI.
            </p>
        </Container>
        </Jumbotron >
            <Row >
            <Card  style={{ width: '18rem', margin:"auto", marginBottom:"140px", marginTop:"20px"}}>
                <Card.Img variant="top" src="data:image/jpeg;base64," />
                    <Card.Body>
                        <Card.Title>Generation 1</Card.Title>
                    <Card.Text>
                        Pokémon from #1-151!
                    </Card.Text>
                        <Button style = {{ backgroundColor:"#09bdb7", border:"none"  }} onClick={gen1}>Find Out More!</Button>
                    </Card.Body>
            </Card>
            <Card style={{ width: '18rem', margin:"auto", marginBottom:"140px", marginTop:"20px"}}>
                <Card.Img variant="top" src="data:image/jpeg;base64," style={{width:"auto", height:"auto"}}/>
                    <Card.Body>
                        <Card.Title>Generation 2</Card.Title>
                    <Card.Text>
                        Pokémon from #152-251!
                    </Card.Text>
                    <Button style = {{ backgroundColor:"#09bdb7", border:"none"  }} onClick={gen2}>Find Out More!</Button>
                    </Card.Body>
            </Card>
            <Card style={{ width: '18rem', margin:"auto", marginBottom:"140px", marginTop:"20px"}}>
                <Card.Img variant="top" src="https://i.etsystatic.com/13572809/r/il/602dcd/1030926128/il_570xN.1030926128_m380.jpg" />
                    <Card.Body>
                        <Card.Title>Generation 3</Card.Title>
                    <Card.Text>
                        Pokémon from #252-386!
                    </Card.Text>
                    <Button style = {{ backgroundColor:"#09bdb7", border:"none"  }} onClick={gen3}>Find Out More!</Button>
                    </Card.Body>
            </Card>
            </Row>
            </div>
         
        </>
    )
}

export default Homepage


